import React, { useEffect, useState } from 'react';
import { Button, Form, Input, message, Modal, Image, Table } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { DeleteOutlined, ExclamationCircleOutlined, EditOutlined, } from '@ant-design/icons';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import MyPagination from '@/components/pagination';
import UploadImage from '@/components/upload';
import dayjs from 'dayjs';
import jwtDecode from 'jwt-decode';
import { handleNotDelete, handleNotUpdate } from '@/utils/prompt';
const { confirm } = Modal;
const { Search } = Input;
interface DataType {
  data: unknown;
  avatar: string | undefined;
  key: React.Key;
  _id?: string;
  username?: string;
  provider: string;
  createTime: string;
  updateTime: string;
}
interface UserData {
  avatar: any;
  code: number;
  _id: any;
  totalCount: number;
  page: number;
  pageSize: number;
  data: DataType[];
}
const UserInfo = (props: any) => {
  const token = jwtDecode(localStorage.getItem('token') as string) as object | any;
  // const role_type = Boolean(token[0].role.map((item: any) => item.role_type).join(''))
  const role_type = token[0].role[0].role_type
  const columns: ColumnsType<DataType> = [
    {
      title: 'ç”¨æˆ·å',
      dataIndex: 'username',
      width: '10rem'
    },
    {
      title: 'ç”¨æˆ·ç±»å‹',
      dataIndex: 'provider',
      width: '6rem'
    },
    {
      title: 'é‚®ç®±',
      dataIndex: 'email',
      width: '15rem'
    },
    {
      title: 'å¤´åƒ',
      dataIndex: 'avatar',
      render: (_, record: any) => {
        if (typeof (record.avatar) === 'object') {
          return <Image width={50} height={50} src={record.avatar[0].thumbUrl} />
        }
        return <Image width={50} height={50} src={record.avatar} />
      },
    },
    {
      title: 'åˆ›å»ºæ—¶é—´',
      dataIndex: 'createTime',
      render: time => {
        return time === 0 ? time : dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ›´æ–°æ—¶é—´',
      dataIndex: 'updateTime',
      render: time => {
        return time === 0 ? time : dayjs(time * 1000).format('YYYY-MM-DD HH:mm:ss');
      },
    },
    {
      title: 'æ“ä½œ',
      key: 'action',
      render: (_, item: any) => {
        return (
          <div>
            <Button
              type="primary"
              danger
              shape="circle"
              icon={<DeleteOutlined />}
              onClick={() => {
                userDelete(item);
              }}
              style={{ marginRight: '5px' }}
            />
            <Button
              type="primary"
              ghost
              shape="circle"
              icon={<EditOutlined />}
              onClick={() => {
                userUpdate(item);
              }}
              style={{ marginRight: '5px' }}
            />
          </div>
        );
      },
    },
  ];
  // è¡¨å•æ•°æ®
  const [updateForm] = Form.useForm();
  // è·å–çš„ç”¨æˆ·è¯¦æƒ…
  const [userData, setUserData] = useState<any>([])
  // ç”¨æˆ·åˆ—è¡¨
  const [list, setList] = useState<DataType[]>([]);
  // åˆ†é¡µæ€»æ•°
  const [total, setTotal] = useState(0);
  // å½“å‰ç¬¬å‡ é¡µ
  const [currentPage, setCurrentPage] = useState(1);
  // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
  const [pageSize, setPageSize] = useState(10);
  // å›¾ç‰‡åˆ—è¡¨
  const [imageList, setImageList] = useState<any>();
  // å›¾ç‰‡åœ°å€
  const [imgUrl, setImgUrl] = useState<any>([]);
  // çª—å£
  const [isUpdateModalOpen, setIsUpdateModalOpen] = useState(false);

  // è·å–ç”¨æˆ·åˆ—è¡¨æ•°æ®
  useEffect(() => {
    props.BlogActions.asyncUserListAction(currentPage, pageSize, '').then((res: UserData) => {
      // è·å–ç”¨æˆ·
      let { data, totalCount, page, pageSize } = res.data as unknown as UserData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  }, [currentPage, pageSize, props.BlogActions]);

  // æ›´æ–°ç”¨æˆ·æ¨¡æ€æ¡†
  const userUpdate = (item: UserData) => {
    updateForm.setFieldsValue(item);
    if (typeof (item.avatar) === 'string') {
      let data = item.avatar;
      let start = data.indexOf('images');
      let name = data.substring(start);
      item.avatar = [
        {
          name: name,
          thumbUrl: item.avatar,
        },
      ];
    }
    setImgUrl(item.avatar);
    setIsUpdateModalOpen(true);
    setUserData(item);
  };
  // æäº¤æ›´æ–°
  const handleUpdateConfirm = () => {
    if (role_type) {
      return handleNotUpdate();
    }
    let data = updateForm.getFieldsValue();

    if (typeof imageList === 'object') {
      data.avatar = imageList.url;
    } else {
      data.avatar = imageList;
    }
    if (imageList === undefined) {
      data.avatar = imgUrl[0].thumbUrl;
    }
    props.BlogActions.asyncUserUpdateAction({
      username: data.username,
      email: data.email,
      avatar: data.avatar,
      //@ts-ignore
      id: userData._id,
    }).then(() => {
      message.success('æ›´æ–°æˆåŠŸ');
      // é‡æ–°è°ƒç”¨æŸ¥è¯¢æ¥å£
      props.BlogActions.asyncUserListAction(currentPage, pageSize, '').then((res: UserData) => {
        // è·å–ç”¨æˆ·
        let { data, totalCount, page, pageSize } = res.data as unknown as UserData;
        setList(data);
        setTotal(totalCount);
        setCurrentPage(page);
        setPageSize(pageSize);
      });
      updateForm.resetFields();
      setIsUpdateModalOpen(false);
    });
  };
  // å…³é—­çª—å£
  const handleUpdateCancel = () => {
    updateForm.resetFields();
    setIsUpdateModalOpen(!isUpdateModalOpen);
  };
  // åˆ é™¤ç”¨æˆ·
  const userDelete = (item: UserData) => {
    confirm({
      title: 'ä½ ç¡®å®šè¦åˆ é™¤å—?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        if (role_type) {
          return handleNotDelete();
        }
        // å…ˆå°†è¦åˆ é™¤çš„æ•°æ®è¿‡æ»¤æ‰å†è°ƒç”¨æ¥å£
        setList(list.filter((it: any) => it._id !== item._id));
        message.success('ç”¨æˆ·åˆ é™¤æˆåŠŸ');
        props.BlogActions.asyncUserDeleteAction(item._id).then(() => {
          props.BlogActions.asyncUserListAction(currentPage, pageSize, '').then((res: UserData) => {
            // è·å–ç”¨æˆ·
            let { data, totalCount, page, pageSize } = res.data as unknown as UserData;
            setList(data);
            setTotal(totalCount);
            setCurrentPage(page);
            setPageSize(pageSize);
          });
        });
      },
    });
  };
  // æœç´¢
  const onSearch = (value: string) => {
    props.BlogActions.asyncUserListAction(currentPage, pageSize, value).then((res: UserData) => {
      let { data, totalCount, page, pageSize } = res.data as unknown as UserData;
      setList(data);
      setTotal(totalCount);
      setCurrentPage(page);
      setPageSize(pageSize);
    });
  };
  // è·³è½¬é¡µæ•°æ®æ˜¾ç¤º
  const onChangePage = (page: number, pageSize: number, params = '') => {
    // é‡æ–°è°ƒç”¨æ¥å£å°†å‚æ•°ä¼ é€’è¿‡å»
    props.BlogActions.asyncUserListAction(page, pageSize, params).then((res: UserData) => {
      // è·å–åˆ—è¡¨æ•°æ®
      let { data } = res.data as unknown as UserData;
      setList(data);
      // åˆ‡æ¢è¡Œ
      setCurrentPage(page);
      // æ ¹æ®é¡µé¢æ•°æ®æ˜¾ç¤ºé¡µç 
      setPageSize(pageSize);
    });
  };
  // è·å–å›¾ç‰‡ä¿¡æ¯
  const handleChange = (data: string) => {
    setImageList(data);
  };
  return (
    <div>
      <div className="title">
        <div></div>
        <Search
          className="search"
          allowClear
          placeholder="è¯·è¾“å…¥ç”¨æˆ·åç§°"
          onSearch={onSearch}
          enterButton
        />
      </div>
      <Modal
        open={isUpdateModalOpen}
        title={<div style={{ textAlign: 'left' }}>æ›´æ–°ç”¨æˆ·</div>}
        okText="æ›´æ–°"
        cancelText="å–æ¶ˆ"
        onCancel={handleUpdateCancel}
        onOk={() => {
          handleUpdateConfirm();
        }}
      >
        <Form form={updateForm} layout="vertical" name="basic" className="userAddFrom">
          <Form.Item
            name="username"
            label="ç”¨æˆ·åç§°"
            rules={[{ required: true, message: 'ç”¨æˆ·åç§°ä¸èƒ½ä¸ºç©º' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="email"
            label="ç”¨æˆ·é‚®ç®±"
            rules={[{ required: true, message: 'é‚®ç®±ä¸èƒ½ä¸ºç©º' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="avatar"
            label="å¤´åƒ"
            rules={[{ required: true, message: 'å¤´åƒä¸èƒ½ä¸ºç©º' }]}
          >
            {/* @ts-ignore */}
            <UploadImage handleChange={handleChange} imgUrlArr={imgUrl} />
            {/* <Input /> */}
          </Form.Item>

        </Form>
      </Modal>
      <Table
        columns={columns}
        dataSource={list}
        scroll={{ y: 'calc(100vh - 240px)', x: true }}
        rowKey={(item: any) => {
          return item._id + Date.now();
        }}
        pagination={false}
      />
      <MyPagination
        pageSize={pageSize}
        currentPage={currentPage}
        total={total}
        onChange={onChangePage}
      ></MyPagination>
    </div>
  );
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};
export default connect(null, mapDispatchToProps)(UserInfo);
